@import 'variables';

@import '_header';
@import 'tabs';
@import 'actions-dropdown';
@import 'global-new-entity-button';
@import 'global-sidebar';

.learndash-screen .wrap h1.wp-heading-inline,
.learndash-screen .page-title-action {
	display: none;
}

/* WP Admin interface must be above header */
#screen-meta-links {
	z-index: 159000;
}
#wpadminbar,
#adminmenuwrap {
	z-index: 159001;
}

#TB_overlay,
.screen-reader-shortcut:focus {
	z-index: 159002;
}

#TB_window {
	z-index: 159003;
}

#screen-meta-links {
	position: relative;
	margin: 0 20px 0 0 !important; // Override standard WP media query margin-bottom: 60px.
}

/* Screen options */
#screen-meta {
	margin: 0 0 0 -20px;
	border-width: 0;
	border-bottom-width: 1px;
}

// If there's an update nag, add spacing
.update-nag {
	margin-bottom: 20px;
	position: relative;
	z-index: 10000;

	& + #sfwd-header:before {
		background: white;
		bottom: 100%;
		content: "";
		display: block;
		height: 100%;
		left: 0;
		position: absolute;
		width: 100%;
		z-index: 0;
	}
}
