/* Gutenberg header styling */
.block-editor-page #sfwd-header {
	position: fixed;
	top: 46px;
	padding-left: 10px;
	width: 100%;

	.ld-global-header {
		position: relative;
	}
}

.block-editor-page.is-fullscreen-mode #sfwd-header {
	top: 0px;
	padding-left: 0;
	width: 100%;
}

@media (min-width: 600px) {

	.branch-5-1,
	.branch-5-2,
	.branch-5-3 {
		&.block-editor-page {
			#sfwd-header {
				top: 0;
			}
		}
	}
}

@media (min-width: 782px) {
	.block-editor-page #sfwd-header {
		width: calc(100% - 36px);
	}
}

@media (min-width: 960px) {
	.block-editor-page #sfwd-header {
		width: calc(100% - 160px);
	}
}

/* Non-Gutenberg header styling */
body:not(.block-editor-page) #sfwd-header {
	box-sizing: border-box;
	margin-left: -20px;
	padding: 8px 20px;
	width: calc(100% + 20px);
	z-index: 9999;
}

body:not(.block-editor-page) .learndash-settings-page-wrap #sfwd-header {
	margin-right: -20px;
	width: calc(100% + 40px);
}

/* Global header styling */
#sfwd-header {
	background-color: white;
	box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
	display: block !important;
	position: sticky;
	top: 32px;
	width: 100%;
	z-index: 99990;
}

.branch-5-1,
.branch-5-2,
.branch-5-3 {
	#sfwd-header {
		left: 0;
	}
}


.learndash-user-group-leader {
	.ld-global-header {
		margin-top: 25px;
	}
}

/* Global header inner */
.ld-global-header {
	align-items: center;
	// display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	height: auto;
	width: 100%;

	h1 {
		color: #23282d;
		display: block;
		line-height: 1.4em;
		font-weight: 600;
		font-size: 2em;
		margin: 7px;
		font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	}

	/* Button styles overrides */
	a.button,
	button.button {
		background-color: transparent !important;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		color: $wp-blue;
		font-weight: 700;
		text-shadow: none !important;
		&.is-primary {
			box-shadow: inset 0px 4px 0 0 white, 0px 4px 0 0 $wp-blue !important;
			color: #555;
		}

		&.is-primary:hover,
		&.is-primary:focus {
			background-color: transparent;
			box-shadow: inset 0px 4px 0 0 white,0px 4px 0 0 $wp-blue !important;
			color: #555;
		}

		&.components-button {
			&.is-primary:hover,
			&.is-primary:focus {
				color: #555;
			}
		}
	}
}

/* Back link */
.ld-back-link {
	flex-basis: 100%;
	text-decoration: none;
	&:hover {
		text-decoration: underline;
	}
}

/* Gutenberg screen options zindex */
.components-modal__screen-overlay {
	z-index: 159000;
}

/* Default WP editor styles overrides for Gutenberg, shift down elements */
.ld-header-has-tabs {
	.edit-post-layout {
		padding-top: 131px;

		.editor-post-publish-panel {
			top: 178px;

			@media (min-width: 782px) {
				top: 159px;
			}
		}

		.edit-post-header {
			top: 176px;

			@media (min-width: 782px) {
				top: 158px;
			}
		}

		.edit-post-layout__content {
			@media (min-width: 782px) {
				min-height: calc(100% - 214px);
				top: 214px;
			}
		}
	}

	&.branch-5 {
		.edit-post-layout__content {
			@media (min-width: 782px) {
				min-height: calc(100% - 182px);
				top: 182px;
			}
		}

		.edit-post-layout {
			@media (min-width: 600px) {
				padding-top: 0;
			}

			&.has-fixed-toolbar {
				@media (min-width: 600px) {
					padding-top: 0;
				}
			}
		}
	}

	&.branch-5-1,
	&.branch-5-2,
	&.branch-5-3 {
		.edit-post-layout {
			padding-top: 131px;

			@media (min-width: 600px) {
				padding-top: 181px;
			}

			&.has-fixed-toolbar{
				@media (min-width: 600px) {
					padding-top: 181px;
				}
			}
		}

		&.is-fullscreen-mode {
			.edit-post-layout {
				.edit-post-header {
					@media (min-width: 782px) {
						top: 135px;
					}
				}

				.edit-post-layout__content {
					@media (min-width: 782px) {
						top: 190px;
					}
				}

				.edit-post-sidebar {
					@media (min-width: 782px) {
						top: 190px;
					}
				}
			}
		}
	}

	&.is-fullscreen-mode .edit-post-sidebar,
	.edit-post-sidebar {
		@media (min-width: 782px) {
			top: 214px;
		}
	}

	&.is-fullscreen-mode {
		.edit-post-layout {
			.editor-post-publish-panel {
				@media (min-width: 782px) {
					top: 117px;
				}
			}
		}
	}
}

.edit-post-layout {

	@media (min-width: 600px) {
		padding-top: 143px;
	}

	&.has-fixed-toolbar {

		@media (min-width: 600px) {
			padding-top: 143px;
		}
	}

	.editor-post-publish-panel {
		top: 130px;

		@media (min-width: 782px) {
			top: 120px;
		}
	}

	.edit-post-header {
		top: 128px;

		@media (min-width: 782px) {
			top: 120px;
		}
	}
}

.edit-post-sidebar {
	@media (min-width: 782px) {
		top: 176px;
	}
}

.edit-post-layout__content {
	@media (min-width: 782px) {
		min-height: calc(100% - 176px);
		top: 176px;
	}
}

.branch-5 {
	.edit-post-layout__content {
		@media (min-width: 782px) {
		top: 176px;
		}
	}
}

/* When the header error boundary is displayed */
body.block-editor-page #sfwd-header .header-boundary {
	position: absolute;
	top: 5em;
	left: 13.8em;
}
