/* Hidden tabs */
.tab-content[aria-hidden="true"] {
	height: 0;
	left: -999em;
	overflow: hidden;
	position: absolute !important;
	top: -999em;
}

/* Hide all metaboxes before the tabs are ready */
#normal-sortables,
.metabox-location-normal {
	opacity: 0;
	.ld-tabs-ready & {
		opacity: 1;
	}
}

/* Tab buttons/links */
@keyframes LDFade {
	from { opacity: .3; }
	to { opacity: .6; }
}
.ld-tab-buttons {
	border-top: 1px solid #e2e4e7;
	flex: 1 0 100%;
	position: relative;

	a.button,
	button.button {
		animation: LDFade .4s ease-in 0s infinite alternate;
		margin-right: 15px;
		opacity: .7;
		pointer-events: none;
		.ld-tabs-ready & {
			animation: none;
			opacity: 1;
			pointer-events: all;
			margin-top: 10px;
		}

		&:focus {
			background-color: transparent;
			box-shadow: none;
		}

		&:hover {
			background-color: transparent;
		}
	}

}

