/* Show/hide metaboxes depending on tab view in Gutenberg and Classic editors and standalone page */
.sfwd-courses_page_courses-builder,
body[data-active-tab]:not([data-active-tab="post-body-content"]) {
	#learndash_admin_quiz_navigation,
	// Classic editor
	#commentstatusdiv,
	#commentsdiv,
	#slugdiv,
	#authordiv,
	#revisionsdiv,
	#side-sortables .postbox,
	// Gutenberg
	.components-panel__body,
	// .components-notice-list,
	#learndash_course_navigation_admin_meta {
		display: none;
	}
	.edit-post-header-toolbar {
		opacity: 0;
		visibility: hidden;
	}
	.edit-post-layout__metaboxes {
		border-top: none;
		padding: 0;

		.edit-post-meta-boxes-area {
			margin: auto;
		}
	}
	button.edit-post-sidebar__panel-tab[data-label="Block"] {
	  display: none;
	}
	.post-publish-panel__postpublish .components-panel__body.is-opened {
		display: block;
	}
	&[data-active-tab="learndash_quiz_builder"],
	&[data-active-tab="learndash_course_builder"],
	&[data-active-tab="sfwd-quiz_page_quizzes-builder"] {
		#side-sortables #sfwd-course-lessons.postbox,
		#side-sortables #sfwd-course-topics.postbox,
		#side-sortables #sfwd-course-quizzes.postbox,
		#side-sortables #sfwd-quiz-questions.postbox,
		#sfwd-course-lessons,
		#sfwd-course-topics,
		#sfwd-course-quizzes,
		#sfwd-quiz-questions {
			display: block !important;
		}
	}
}

#sfwd-course-lessons,
#sfwd-course-topics,
#sfwd-course-quizzes,
#sfwd-quiz-questions {
	display: none;
}

// Publish and Save settings needs to be always visible
#side-sortables #submitdiv.postbox {
	display: block !important;
}

// Hide metabox toggle header
div#learndash_quiz_builder > .inside,
div#learndash_course_builder > .inside {
	display: block !important;
}

div#learndash_quiz_builder > .handlediv,
div#learndash_quiz_builder > .hndle,
div#learndash_course_builder > .handlediv,
div#learndash_course_builder > .hndle {
    display: none !important;
}
