/* Action button and dropdown position */
.ld-tab-buttons .edit-post-header__settings {
	position: absolute;
	top: -45px;
	right: 20px;
}

/* Action button */
.ld-actions {
	svg {
		align-self: center;
		margin-left: 10px;
	}
}

/* Dropdown styling */
.ld-popover.components-popover {
	left: auto;
	right: 40px;
	top: 100px !important;

	.components-menu-item__button {
		box-sizing: border-box;
		display: inline-flex;
		-webkit-appearance: none;
		background: 0 0;
		border: 0;
		color: #40464d;
		cursor: pointer;
		font-size: 13px;
		margin: 0;
		padding: 8px 8px 8px 2rem;
		text-align: left;
		text-decoration: none;
		width: 100%;
		&.active {
			color: #007cba;
		}
	}
}

.ld-popover.components-popover .delete {
	color: red;
}

/* Dropdown styling for non-Gutenberg pages and single Quiz edit Gutenberg pages */
body:not(.block-editor-page),
.sfwd-quiz.block-editor-page {
	.ld-popover.components-popover {
		background: white;
		border: 1px solid #e2e4e7;
		box-shadow: 0 3px 30px rgba(25,30,35,.1);
		position: absolute;
		right: 11px;
		top: 100% !important;
		width: 260px;
		opacity: 1;
	}
	.components-popover:not(.is-mobile).is-left .components-popover__content {
		right: 0;
	}

	.components-popover__content {
		display: flex;
		flex-direction: column;
	}

	.components-popover__content button {
		border: 0;
		background-color: transparent;
		text-align: left;
	}

	.ld-popover.components-popover:after,
	.ld-popover.components-popover:before {
		content: "";
		position: absolute;
		height: 0;
		width: 0;
		line-height: 0;
		border: 8px solid #e2e4e7;
		border-bottom-style: solid;
		border-left-color: transparent;
		border-right-color: transparent;
		border-top: none;
		margin-left: -10px;
		top: -8px;
		right: 10px;
	}

	.ld-popover.components-popover:after {
		border: 8px solid #fff;
		border-bottom-style: solid;
		border-left-color: transparent;
		border-right-color: transparent;
		border-top: none;
		top: -6px;
	}
}
