/* Metabox color code */
/* Associated content color code */
.learndash_navigation_lesson_topics_list .topic_item > a > span:before,
.learndash_navigation_lesson_topics_list .lesson > a:before,
#sfwd-course-topics h2:before,
#sfwd-course-quizzes h2:before,
#sfwd-course-lessons h2:before,
#sfwd-quiz-questions h2:before,
.ld-question-overview-widget-item:before {
	align-items: center;
	border-radius: 100%;
	color: white;
	display: inline-flex;
	font-size: 13px;
	font-weight: bold;
	height: 18px;
	justify-content: center;
	margin-right: 10px;
	text-transform: uppercase;
	width: 18px;
}

.learndash_navigation_lesson_topics_list .lesson > a:before,
#sfwd-course-lessons h2:before {
	content: "L";
	background-color: $code-parent;
}

.learndash_navigation_lesson_topics_list .topic_item > a > span:before,
#sfwd-course-topics h2:before {
	content: "T";
	background-color: $code-child;
}

#sfwd-course-quizzes h2:before {
	content: "Q";
	background-color: $code-grandchild;
}

#sfwd-quiz-questions h2:before,
.ld-question-overview-widget-item:before {
	content: "Q";
	background-color: $code-question;
}
