.global-new-entity-button {
	align-self: flex-end;
	float: right;
	background: #00a0d2;
	border-radius: 2px;
	border: 1px solid #008ec2;
	border: none;
	color: #fff;
	cursor: pointer;
	font-size: 13px;
	font-weight: 600;
	line-height: normal;
	margin-left: 4px;
	margin-right: 135px;
	outline: 0;
	padding: 4px 8px;
	position: relative;
	text-decoration: none;
	text-shadow: none;
	top: -33px;

	&.-right {
		margin-right: 0;
	}

	&:active,
	&:hover {
		background: $wp-blue-hover;
		color: #fff;
	}

	.learndash-post-type & {
		display: none;
	}
}